.sudoku_board {
    display: grid;
    grid-template-columns: repeat(9, 9vh);
    grid-template-rows: repeat(9, 9vh);
    border: 2px solid black;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.new_btn {
    top: 20%;
    transform: translateY(-20%);
}

.solution_btn {
    top: 40%;
    transform: translateY(-40%);
}

.submit_btn {
    top: 60%;
    transform: translateY(-60%);
}

.board_btn {
    position: absolute;
    border: 2px solid black;
    background-color: white;
    width: 25vh;
    height: 10vh;
    font-weight: bold;
    color: black;
    left: -55%;
    transform: translateX(55%);
    padding: 0;
}

.board_btn:hover {
    background-color: black;
    color: white;
}

.message_display {
    background-color: white;
    border: 5px solid black;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    width: 60%;
    height: 50%;
}

.message {
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(-50%, -10%);
    font-size: 2rem;
}

.close_btn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid black;
    background-color: white;
    width: 15vh;
    height: 5vh;
    padding: 0;
}

.close_btn:hover {
    background-color: black;
    color: white;
}

@media only screen and (max-width: 1400px) {
    .sudoku_board {
        grid-template-columns: repeat(9, 7vh);
        grid-template-rows: repeat(9, 7vh);
        left: 50%;
        top: 30%;
        transform: translate(-50%, -30%);
    }

    .new_btn {
        left: -15%;
        transform: translateX(15%);
    }

    .solution_btn {
        left: 30%;
        transform: translateX(-30%);
    }

    .submit_btn {
        left: 75%;
        transform: translateX(-75%);
    }

    .board_btn {
        width: 25vh;
        height: 7vh;
        border: 2px solid black;
        top: 120%;
        transform: translateY(-120%);
    }
}

@media only screen and (max-height: 600px) {

    .message {
        position: absolute;
        left: 50%;
        top: 5%;
        transform: translate(-50%, -5%);
        font-size: 1rem;
    }

    .close_btn {
        width: 10vh;
        height: 5vh;
        font-size: 0.5rem;
    }
}
